/*стили для разработки*/
body {
  margin: 0;
  padding: 0;
  background: #f2f2f2;
  text-transform: uppercase;
}
#app {
  font-family: 'Pt Sans', 'Gerogia', serif;
  font-weight: 700;
  width: 91.5%;
  margin: 0 auto;
  padding: 0;
  background-color: #fefefe;
  -webkit-box-shadow: 0px 0px 20px 1px rgba(0,0,0,0.5);
          box-shadow: 0px 0px 20px 1px rgba(0,0,0,0.5);
}

.welcome {
  background-position: 50% 29%;
  background-repeat: no-repeat;
  margin: 0;
  padding: 0;
  position: relative;
  max-width: 100%;
  overflow: hidden;
}
.welcome-text {
  display: block;
  margin: 76px auto;
  width: 38%;
}
.welcome-decor {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.page-title h1 {
  text-align: center;
  text-transform: uppercase;
  margin-top: 60px;
  font-size: 1.33em;
}
.page-title p {
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  margin-top: 29px;
  font-size: 0.88em;
}
.first-feature {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 4.9%;
  height: 255px;
  background: #81b3d2;
  color: #fff;
}
.first-feature img {
  margin-top: -10%;
}
.feature-img-mask {
  width: 66.66666%;
  overflow: hidden;
}
.feature-item {
  width: 33.33333%;
  text-align: center;
  text-transform: uppercase;
}
.feature-item h2 {
  text-transform: uppercase;
  text-align: center;
  font-size: 1.33em;
  margin-top: 11.2%;
  line-height: 1.04em;
}
.feature-item span {
  display: block;
  text-align: center;
  font-weight: normal;
  margin-top: 6%;
  font-size: 0.9em;
}
.feature-item p {
  text-transform: uppercase;
  text-align: center;
  margin-top: 5.6%;
  line-height: 1.5em;
  font-size: 0.88em;
  font-weight: normal;
}
.advantages {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.advantage {
  width: 33.3333333%;
  text-transform: uppercase;
  padding-bottom: 5.6%;
}
.advantage img {
  display: block;
  margin: 0 auto;
  margin-top: 15%;
  height: 28%;
}
.advantage h2 {
  text-align: center;
  margin-top: 6.5%;
  font-size: 1.3em;
}
.advantage p {
  text-align: center;
  font-weight: normal;
  font-size: 0.90em;
  margin-top: 5.5%;
}
.second-feature {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 256px;
  color: #fff;
  background: #81b3d2;
}
.second-feature img {
  margin-top: -75px;
}
.features-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #eee;
  height: 255px;
}
.hotels {
  text-align: center;
  position: relative;
}
.hotels-title {
  margin-top: 4.2%;
  font-size: 1.88em;
  margin-bottom: 0;
}
.hotels-text {
  font-size: 0.88em;
  font-weight: normal;
  margin-top: 1.9%;
  line-height: 1.7em;
}
.btn-hotels {
  display: block;
  margin: 0 auto;
  margin-top: 4%;
  width: 63%;
  background: gray;
  border: 0;
  font-weight: bold;
  text-transform: uppercase;
  width: 47%;
  padding-top: 2.4%;
  padding-bottom: 2.4%;
  font-size: 1.32em;
  color: #fff;
  font-family: inherit;
  background: #766357;
}
#map {
  background: #f3f3f3;
}

.main-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-transform: uppercase;
}
.main-nav-item {
  padding-top: 18px;
  padding-bottom: 20px;
  font-size: 0.90rem;
  width: 20%;
  text-align: center;
}
.main-nav-item a {
  text-decoration: none;
  color: #000;
}
.main-nav-item:nth-child(3) {
  margin: 0;
  padding: 0;
  position: relative;
  font-size: 0;
}
.main-nav-item:nth-child(3) a {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  top: -29px;
}
.main-nav-item:nth-child(3) a img{
  width: 138px;
  height: auto;
}

.search-form {
  width: 40%;
  margin: 0 auto;
  font-size: inherit;
  padding: 4.2% 3.5%;
  font-size: 0.899em;
}
.search-form fieldset {
  border: 0;
  margin: 0;
  position: relative;
  margin-bottom: 3%;
}
.search-form #date-in {
  margin-left: 0.8%;
}
.search-form input {
  background-color: lightblue;
  border: none;
  padding: 2%;
  width: 71%;
}
.search-form label {
  margin-right: 3.3%;
  margin-left: 0.2%;
}
































